export enum PermitType {
  AMOUNT = 1,
  ALLOWED = 2,
}
export interface PermitInfo {
  type: PermitType
  name: string
  // version is optional, and if omitted, will not be included in the domain
  version?: string
}
interface multiChainPermitInfo {
  [chainId: number]: PermitInfo
}

interface Detail {
  name: string
  symbol: string
}

