import { SupportedChainId } from '../constants/chains'

export function constructSameAddressMap<T extends string>(
  address: T,
  includeArbitrum: boolean
): { [chainId: number]: T } {
  if (includeArbitrum)
    return {
      [SupportedChainId.MAINNET]: address,
      [SupportedChainId.ROPSTEN]: address,
      [SupportedChainId.RINKEBY]: address,
      [SupportedChainId.GOERLI]: address,
      [SupportedChainId.KOVAN]: address,
      [SupportedChainId.BSCTEST]: address,
      [SupportedChainId.BSCMAIN]: address,
      [SupportedChainId.HECOTEST]: address,
      [SupportedChainId.HECOMAIN]: address,
      [SupportedChainId.CLOVERTEST]: address,
      [SupportedChainId.HAIC]: address,
      [SupportedChainId.GALT]: address,
      [SupportedChainId.COINEXTEST]: address,
      [SupportedChainId.MATIC]: address,
      [SupportedChainId.MATICTEST]: address,
      [SupportedChainId.FANTOM]: address,
      [SupportedChainId.FANTOMTEST]: address,
      [SupportedChainId.HSC]: address,
      [SupportedChainId.HSCTEST]: address,
      [SupportedChainId.OKTEST]: address,
      [SupportedChainId.XDAI]: address,
      [SupportedChainId.HARMONY]: address,
      [SupportedChainId.HARMONYTEST]: address,
      [SupportedChainId.ARBITRUM_ONE]: address,
      [SupportedChainId.AVALANCHE]: address,
      [SupportedChainId.AVALANCHETEST]: address,
      [SupportedChainId.CELO]: address,
      [SupportedChainId.CELOTEST]: address,
    }
  return {
    [SupportedChainId.MAINNET]: address,
    [SupportedChainId.ROPSTEN]: address,
    [SupportedChainId.RINKEBY]: address,
    [SupportedChainId.GOERLI]: address,
    [SupportedChainId.KOVAN]: address,
    [SupportedChainId.BSCTEST]: address,
    [SupportedChainId.BSCMAIN]: address,
    [SupportedChainId.HECOTEST]: address,
    [SupportedChainId.HECOMAIN]: address,
    [SupportedChainId.CLOVERTEST]: address,
    [SupportedChainId.HAIC]: address,
    [SupportedChainId.GALT]: address,
    [SupportedChainId.COINEXTEST]: address,
    [SupportedChainId.MATIC]: address,
    [SupportedChainId.MATICTEST]: address,
    [SupportedChainId.FANTOM]: address,
    [SupportedChainId.FANTOMTEST]: address,
    [SupportedChainId.HSC]: address,
    [SupportedChainId.HSCTEST]: address,
    [SupportedChainId.OKTEST]: address,
    [SupportedChainId.XDAI]: address,
    [SupportedChainId.HARMONY]: address,
    [SupportedChainId.HARMONYTEST]: address,
    [SupportedChainId.AVALANCHE]: address,
    [SupportedChainId.AVALANCHETEST]: address,
    [SupportedChainId.CELO]: address,
    [SupportedChainId.CELOTEST]: address,
  }
}
