import { useActiveWeb3React } from '../../hooks/web3'
import React, { useEffect } from 'react'
import styled from 'styled-components/macro'
import { ReactComponent as Close } from '../../assets/images/x.svg'
import usePrevious from '../../hooks/usePrevious'
import { ApplicationModal } from '../../state/application/actions'
import { useModalOpen, useNetworkModal } from '../../state/application/hooks'
import { Trans } from '@lingui/macro'
import { SUPPORTED_CHAIN_IDS, NETWORK_URLS } from '../../connectors'
import { SupportedChainId } from '../../constants/chains'
import { NETWORK_LABELS, NATIVE_LOGO, ETHERSCAN_PREFIXES, NATIVE_TOKEN_NAME } from '../../constants/addresses'

import Modal from '../Modal'
import Option from '../WalletModal/Option'

const CloseIcon = styled.div`
  position: absolute;
  right: 1rem;
  top: 14px;
  &:hover {
    cursor: pointer;
    opacity: 0.6;
  }
`

const CloseColor = styled(Close)`
  path {
    stroke: ${({ theme }) => theme.text4};
  }
`

const Wrapper = styled.div`
  ${({ theme }) => theme.flexColumnNoWrap}
  margin: 0;
  padding: 0;
  width: 100%;
`

const HeaderRow = styled.div`
  ${({ theme }) => theme.flexRowNoWrap};
  padding: 1rem 1rem;
  font-weight: 500;
  color: ${(props) => (props.color === 'blue' ? ({ theme }) => theme.primary1 : 'inherit')};
  ${({ theme }) => theme.mediaWidth.upToMedium`
    padding: 1rem;
  `};
`

const ContentWrapper = styled.div`
  background-color: ${({ theme }) => theme.bg0};
  padding: 1rem;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  overflow: scroll;
  max-height: 400px;
`

const UpperSection = styled.div`
  position: relative;

  h5 {
    margin: 0;
    margin-bottom: 0.5rem;
    font-size: 1rem;
    font-weight: 400;
  }

  h5:last-child {
    margin-bottom: 0px;
  }

  h4 {
    margin-top: 0;
    font-weight: 500;
  }
`

const HoverText = styled.div`
  text-decoration: none;
  color: ${({ theme }) => theme.text1};
  display: flex;
  align-items: center;

  :hover {
    cursor: pointer;
  }
`
const OptionGrid = styled.div`
  display: grid;
  grid-gap: 10px;
  margin-bottom: 10px;
  ${({ theme }) => theme.mediaWidth.upToMedium`
    grid-template-columns: 1fr;
    grid-gap: 10px;
  `};
`

export default function NetworkModal() {
  // important that these are destructed from the account-specific web3-react context
  const { account, chainId, library } = useActiveWeb3React()

  const walletModalOpen = useModalOpen(ApplicationModal.NETWORK)
  const toggleNetworkModal = useNetworkModal()

  const previousAccount = usePrevious(account)

  const toHex = (num: number) => {
    return '0x' + num.toString(16)
  }
  // close on connection, when logged out before
  useEffect(() => {
    if (account && !previousAccount && walletModalOpen) {
      toggleNetworkModal()
    }
  }, [account, previousAccount, toggleNetworkModal, walletModalOpen])

  const addToNetwork = (chainId: SupportedChainId) => {
    const params = {
      chainId: toHex(chainId), // A 0x-prefixed hexadecimal string
      chainName: NETWORK_LABELS[chainId],
      nativeCurrency: {
        name: NATIVE_TOKEN_NAME[chainId].name,
        symbol: NATIVE_TOKEN_NAME[chainId].symbol, // 2-6 characters long
        decimals: 18,
      },
      rpcUrls: [NETWORK_URLS[chainId]],
      blockExplorerUrls: ['https://' + ETHERSCAN_PREFIXES[chainId]],
    }

    if (library && library.provider.isMetaMask && library.provider.request) {
      library.provider
        .request({
          method: 'wallet_addEthereumChain',
          params: [params, account],
        })
        .then((result) => {
          toggleNetworkModal()
          console.log(result)
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }

  function getModalContent() {
    return (
      <UpperSection>
        <CloseIcon onClick={toggleNetworkModal}>
          <CloseColor />
        </CloseIcon>
        <HeaderRow>
          <HoverText>
            <Trans>Connect to a network</Trans>
          </HoverText>
        </HeaderRow>
        <ContentWrapper>
          {SUPPORTED_CHAIN_IDS.map((id, i) => {
            return (
              <OptionGrid key={i}>
                <Option
                  onClick={() => {
                    addToNetwork(id)
                  }}
                  clickable={true}
                  id={`connect-${NETWORK_LABELS[id]}`}
                  color={'#ffffff'}
                  active={chainId !== undefined && chainId === id}
                  header={NETWORK_LABELS[id]}
                  subheader={null}
                  icon={NATIVE_LOGO[id]}
                />
              </OptionGrid>
            )
          })}
        </ContentWrapper>
      </UpperSection>
    )
  }

  return (
    <Modal isOpen={walletModalOpen} onDismiss={toggleNetworkModal} minHeight={false} maxHeight={90}>
      <Wrapper>{getModalContent()}</Wrapper>
    </Modal>
  )
}
